
import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

function checkAuth(next) {
  const token = localStorage.getItem("access");
  if (!token) {
    return next();
  } else {
    next("/dashboard");
  }
}


const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes: [

    {
      path: '',
      component: () => import('./layouts/full/Full'),
      children: [
        {
          path: '/',
          name: 'home',
          redirect: '/dashboard',
          // component: () => import('./views/pages/Home.vue'),
          meta: {
            rule: 'editor'
          }
        },
      ]
    },

    {
    // =============================================================================
    // MAIN LAYOUT ROUTES
    // =============================================================================
      path: '',
      component: () => import('./layouts/main/Main.vue'),
      children: [
        // =============================================================================
        // Theme Routes
        // =============================================================================

        {
          path: '/dashboard',
          name: 'dashboard',
          component: () => import('./views/Dashboard.vue'),
          meta: {
            rule: 'editor'
          }
        },

        {
          path: '/product',
          redirect: '/dashboard',
          component: () => import('./views/pages/product/Product'),
          meta: {
            rule: 'editor'
          },
          children:[
            {
              path: ':productId/podcasts',
              component: () => import('./views/pages/product/podcast/Podcast'),
              meta: {rule: 'editor'},
              children: [
                {
                  path: '',
                  name: 'podcast-index',
                  component: () => import('./views/pages/product/podcast/Podcasts'),
                  meta: {rule: 'editor'},
                },
                {
                  path: ':itemFid',
                  name: 'podcast-item',
                  component: () => import('./views/pages/product/podcast/PodcastItem'),
                  meta: {rule: 'editor'},
                },
              ]
            },


          ]
        },

        {
          path: '/profile',
          component: () => import('./views/pages/profile/Profile'),
          meta: {
            rule: 'editor'
          },
          children:[
            {
              path: '',
              name: 'profile-index',
              component: () => import('./views/pages/profile/ProfileIndex'),
              meta: {
                rule: 'editor'
              }
            }
          ]
        },

        {
          path: 'library',
          component: () => import('./views/pages/library/Library'),
          meta: {
            rule: 'editor'
          },
          children: [
            {
              path: '',
              name: 'library-index',
              component: () => import('./views/pages/library/LibraryIndex'),
              meta: {
                rule: 'editor'
              },
            },

            {
              path: 'favorite/podcasts',
              name: 'podcast-favorite',
              component: () => import('./views/pages/library/favorites/PodcastFavorites'),
              meta: {
                rule: 'editor'
              }
            },

          ]
        },

        {
          path: '/writing',
          redirect: '/writing/',
          component: () => import('./views/pages/writing/Writing'),
          meta: {
            rule: 'editor'
          },
          children:[
            {
              path: '',
              name: 'writing-index',
              component: () => import('./views/pages/writing/Index'),
              meta: {
                rule: 'editor'
              }
            },
            {
              path: 'test',
              name: 'writing-test',
              component: () => import('./views/pages/writing/WritingTest'),
              meta: {
                rule: 'editor'
              }
            },
            {
              path: 'list',
              name: 'writing-list',
              component: () => import('./views/pages/writing/WritingList'),
              meta: {
                rule: 'editor'
              }
            },
            {
              path: 'result',
              name: 'writing-result',
              component: () => import('./views/pages/writing/WritingResult'),
              meta: {
                rule: 'editor'
              }
            }
          ]
        },

        // =============================================================================
        // Pages Routes
        // =============================================================================

      ]
    },

    {
      path: '',
      component: () => import('@/layouts/full-page/FullPage.vue'),
      children: [

        {
          path: '/login',
          name: 'login',
          component: () => import('./views/pages/auth/login/Login.vue'),
          meta: {
            rule: 'editor'
          },

        },
        {
          path: '/register',
          name: 'register',
          component: () => import('./views/pages/auth/register/Register.vue'),
          meta: {
            rule: 'editor'
          }
        },
        {
          path: '/404',
          name: '404',
          component: () => import('@/views/pages/Error404.vue'),
          meta: {
            rule: 'editor'
          }
        }
      ],
    },

    {
      path: '*',
      redirect: '/404'
    }
  ]
})

router.afterEach(() => {
  // Remove initial loading
  const appLoading = document.getElementById('loading-bg')
  if (appLoading) {
    appLoading.style.display = 'none'
  }
})

router.beforeEach((to, from, next) => {
  return next()
})

export default router
